.elements{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(282px, 1fr));
    gap: 19px 17px;
    max-width: 880px;
    padding: 47.25px 0 0;
    margin: 0 auto;
}

@media screen and (max-width: 767px) {
    .elements{
        padding: 36px 19px 0;
        gap: 20px;
    }
}